header {
  width: 95%;
  height: 10%;
  display: flex;
  justify-content: flex-end;
  padding: 2em;
}

header > input {
  background-color:rgb(39, 37, 87);
  color: white;
  padding: .5em 1em;
  border-radius: 10px;
  outline: none;
  border: none;
}

.movies {
  display: flex;
  flex-wrap: wrap;
  width: 95%;
  margin: 0 auto;
  justify-content: space-between;
}

.movie__block {
  width: 300px;
  color: white;
  background-color: rgb(39, 37, 87);
  display: flex;
  flex-direction: column;
  padding: 1em;
  margin: 1em 0.5em;
}

.movie__block > img {
  max-width: 100%;
  max-height: 95%;
}

.movie__block > h3 {
  width: 100%;
  margin: 10px 0;
}

input[type='btn'] {
  cursor: pointer;
}